import { HostListener, AfterViewInit, Component } from '@angular/core';
import Quagga from 'quagga';
import { BeepService } from './beep.service';
import { UpdateService } from './update.service';
declare var $: any;
// https://blog.classycode.com/scanning-barcodes-in-progressive-web-apps-using-angular-5d5a9d50bd8
// https://github.com/serratus/quaggaJS

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
})
export class AppComponent implements AfterViewInit {
  errorMessage: string = '';

  barcode: string = 'Chưa có kết quả';

  barcodes: string[] = [];

  isCompeleted = false;

  private lastScannedCode: string = '';
  private lastScannedCodeDate: number = -1;

  screenHeight: number = 0;
  screenWidth: number = 0;
  @HostListener('window:resize', ['$event'])
  getScreenSize(event?) {
    this.screenHeight = window.innerHeight;
    this.screenWidth = window.innerWidth;
    console.log(this.screenHeight, this.screenWidth);
  }

  constructor(
    private beepService: BeepService,
    private updateService: UpdateService
  ) {
    this.getScreenSize();
  }

  ngAfterViewInit(): void {
    if (
      !navigator.mediaDevices ||
      !(typeof navigator.mediaDevices.getUserMedia === 'function')
    ) {
      this.errorMessage = 'getUserMedia is not supported';
      return;
    }

    this.scanAgain();

    setTimeout(() => {
      this.updateService.checkForUpdates();
    }, 10000);
  }

  onBarcodeScanned(code: string) {
    // ignore duplicates for an interval of 1.5 seconds
    const now = new Date().getTime();
    if (code === this.lastScannedCode && now < this.lastScannedCodeDate + 500) {
      return;
    }

    this.lastScannedCode = code;
    this.lastScannedCodeDate = now;
    this.beepService.beep();

    this.isCompeleted = true;
    Quagga.stop();

    this.barcode = code;
    this.barcodes.push(code);

    $('#optionsModal').modal('show');
    $('#barcode-value').html('Barcode: ' + code);
  }

  scanAgain() {
    this.isCompeleted = false;
    this.barcode = 'Chưa có kết quả';

    // this.errorMessage = "W: " + this.screenWidth + " | H: " + this.screenHeight;
    Quagga.init(
      {
        inputStream: {
          constraints: {
            facingMode: 'environment', // Cho camera sau - user là camera trước,
            width: 100,
            height: this.screenWidth,
          },
          area: {
            // defines rectangle of the detection/localization area
            top: '0%', // top offset
            right: '0%', // right offset
            left: '0%', // left offset
            bottom: '0%', // bottom offset
          },
        },
        decoder: {
          readers: [
            'code_128_reader ',
            'ean_reader',
            // {
            //   format: "ean_reader",
            //config: {
            //  supplements: [
            //    'ean_5_reader', 'ean_2_reader'
            //  ]
            //},
            'ean_8_reader',
            'code_39_reader',
            'code_39_vin_reader',
            'codabar_reader',
            'upc_reader',
            'upc_e_reader',
            'i2of5_reader',
            '2of5_reader',
            'code_93_reader',
          ],
        },
      },
      (err) => {
        if (err) {
          this.errorMessage = `QuaggaJS could not be initialized, err: ${err}`;
        } else {
          Quagga.start();
          Quagga.onDetected((res) => {
            this.onBarcodeScanned(res.codeResult.code);
          });
        }
      }
    );
  }
}
